.start-page {
  display: flex;
  justify-content: center;
  align-items: center;

  min-width: 100vw;
  min-height: 100vh;

  background: radial-gradient(
    circle,
    rgba(10, 0, 162, 1) 0%,
    rgba(166, 0, 255, 1) 100%
  );

  font-family: 'Montserrat', sans-serif;

  &__types {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__title {
    color: white;
    font-size: 35px;
    text-decoration: underline;
  }

  &__pages {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 100px;
  }

  &__pages-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  &__pages-item {
    list-style: none;
  }

  &__pages-link {
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;

    &:hover {
      text-shadow: red 1px 0 10px;
    }
  }
}
